.header-wrapper {
  background-color: rgba(15, 15, 15, 0.95);
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 9;
}

.header-wrapper .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 64px;
  align-items: center;
}
.header-wrapper .header a {
  position: relative;
  color: #fff;
  text-decoration: none;
  padding: 10px 5px;
  font-weight: 600;
  font-size: 17px;
  margin: 0 10px;
}
.header-wrapper .header a:after {
  content: "";
  height: 2px;
  left: 0px;
  margin-top: 0.4rem;
  position: absolute;
  top: 100%;
  transform: scaleX(0);
  width: 100%;
  transition: transform 0.25s ease 0s;
  background-color: rgb(161, 236, 247);
  margin-top: 0px;
  top: calc(100% - 2px);
}
.header-wrapper .header a:hover {
  color: rgb(161, 236, 247);
}
.header-wrapper .header a:hover:after {
  transform: scaleX(1);
}
.header-wrapper .header .profile span {
  color: #fff;
  margin-left: 10px;
  font-size: 20px;
}

.header-wrapper .header .profile {
  display: flex;
  align-items: center;
}
.header-wrapper .header .profile img {
  border-radius: 50%;
  width: 48px;
  height: 48px;
}
.content {
  margin: 50px auto;
  max-width: 650px;
  text-align: justify;
}
.content a {
  position: relative;
  padding: 3px 0;
  
}
.content .title-detail {
  font-size: 40px;
  margin-bottom: 20px;
}
.content .date-detail {
  color: #757575;
  font-size: 12px;
}
.content .content-detail {
  font-size: 22px;
}
.content div {
  font-size: 20px;
  text-align: justify;
}
.content div strong {
  font-size: 34px;
}
.content div a{
  position: relative;
  text-decoration: none;
  padding: 5px 0;
}
.content div a:after{
  content: "";
  height: 2px;
  left: 0px;
  margin-top: 0.4rem;
  position: absolute;
  top: 100%;
  transform: scaleX(0);
  width: 100%;
  transition: transform 0.25s ease 0s;
  background-color: rgb(161, 236, 247);
  margin-top: 0px;
  top: calc(100% - 2px);
}
.content div a:hover:after {
  transform: scaleX(1);
}

.card {
  box-shadow: 0 1px 3px rgba(18,18,18,0.12);
}
.card-block a {
  font-weight: 600;
  font-size: 26px;
  position: relative;
  color: #212529;
  padding: 3px 0;
}
.card .card-date {
  color: #757575;
  font-size: 12px;
}
.card-text {
  text-align: justify;
  color: #757575;
  min-height: 50px;
  font-size: 20px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.card-block img {
  width: 100%;
}
.footer {
  position: fixed;
  bottom: 0;
  z-index: 1;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #d4d4d4;
  width: 100%;
  background: #fff;
}
.footer a {
  color: #000;
  font-size: 22px;
  margin: 0 20px;
}
a.cool-link:after{
  content: "";
  height: 2px;
  left: 0px;
  margin-top: 0.4rem;
  position: absolute;
  top: 100%;
  transform: scaleX(0);
  width: 100%;
  transition: transform 0.25s ease 0s;
  background-color: rgb(161, 236, 247);
  margin-top: 0px;
  top: calc(100% - 2px);
}
a.cool-link:hover {
  text-decoration: none;
}
a.cool-link:hover:after {
  transform: scaleX(1);
}
.post {
  max-width: 650px;
  margin-top: 50px !important;
  margin-bottom: 50px !important;
}
.post .search-input-wrapper {
  margin-bottom: 24px;
  position: relative;
}
.post .search-input-wrapper .fa-search{
  position: absolute;
  top: 11px;
  left: 10px;
  color: #212529;
}
.post .search-input {
  width: 100%;
  height: 40px;
  padding: 0 30px;
  font-size: 17px;
  font-weight: 500;
  border-radius: 4px;
  border: 1px solid rgba(0,0,0,.125);
}
.home-img {
  text-align: center !important;
}
.home-img img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}
@media only screen and (max-width: 500px) {
  .header-wrapper .header .profile span {
    display: none;
  }
  .card {
    flex-direction: column;
  }
  .card img {
    width: 100%;
  }
  .card .card-right{
    margin-left: 0;
  }
}
@media only screen and (max-width: 768px) {
  .header-wrapper .header {
    height: 48px;
  }
  .header-wrapper .header .profile img {
    width: 32px;
    height: 32px;
  }
  .content {
    max-width: 500px!important;
    text-align: justify;
  }
  .content a {
    position: relative;
    padding: 3px 0;
  }
  .content .content-detail, .title-detail, .date-detail{
    padding: 0 20px;
  } 
}
@media only screen and (max-width: 1000px) {
  .container {
    max-width: unset !important;
  }
}